.widget_cont {
    .row { flex-wrap: wrap; column-gap: 20px; row-gap: 20px;
        .col { min-width: calc(50% - 10px) !important; max-width: calc(50% - 10px) !important; margin: 0;
            .card_box { min-height: 316px; padding: 20px !important;
                .cont_head { font-size: 16px; font-weight: 700; color: #000000;}
            }
            .cont { display: flex; flex-direction: column; padding-top: 40px; 
                > .num { margin-bottom: 20px; border: none; font-size: 64px; font-weight: 700; color: #0067AC;}
                > .cont_detail { display: flex; flex: 1; column-gap: 12px; flex-wrap: wrap; row-gap: 12px;
                    > div { width: calc(33.33% - 8px); min-width: calc(33.33% - 8px); max-width: calc(33.33% - 8px); flex: 1; border-radius: 12px; text-align: center; padding: 20px 8px; cursor: pointer;
                        .num { border: none; padding: 31px 0 0; color: #000000; font-size: 24px; font-weight: 700; background-position: top center; background-repeat: no-repeat;}
                        p { color: #999999; font-size: 14px; font-weight: 400; line-height: 20px;}
                        &.processing { background-color: #E5EFFF;
                            .num { background-image: url("../../../assets/images/ico_status_processing.svg");}
                        }
                        &.approved { background-color: #C6FFF4;
                            .num { background-image: url("../../../assets/images/ico_status_approved.svg");}
                        }
                        &.returned { background-color: #FFE2DE;
                            .num { background-image: url("../../../assets/images/ico_status_returned.svg");}
                        }
                        &.declined { background-color: #FFE9E6;
                            .num { background-image: url("../../../assets/images/ico_status_declined.svg");}
                        }
                        &.completed { background-color: #A0FFEF;
                            .num { background-image: url("../../../assets/images/ico_status_completed.svg");}
                        }
                        &.register { background-color: #D1E4FF;
                            .num { background-image: url("../../../assets/images/ico_status_register.svg");}
                        }
                        &.need_my_apprval { background-color: #F4F3F6;
                            .num { background-image: url("../../../assets/images/ico_status_need_my_approved.svg");}
                        }
                    }
                }
            }
        }
    }
}  

.pointer {
    cursor: pointer;
}